import localStore from './localStore'
import vueItem from '../main'

export function setCartDot () { // 购物车上的小圆点
  let storageCart = getLocalCart()
  let dot = false
  if (storageCart.product.length) {
    dot = true
  } else if (storageCart.promotion.length) {
    dot = storageCart.promotion.some(i => {
      return i.type !== 3
    })
  }
  return dot
}

export function setPromotion (item, isManual = 1) {
  // 对促销数据做处理，加入本地购物车
  item.promotionRuleVO.productList = item.promotionRuleVO.productList ? item.promotionRuleVO.productList : []
  item.promotionRuleVO.productList = item.promotionRuleVO.productList.map(childItem => {
    if (childItem.isHave) {
      childItem.selected = true
    }
    if (childItem.mustQty && item.type !== 4) {
      childItem.selected = true
      childItem.shopCartNum = childItem.mustQty
    }
    if (item.type === 4) {
      item.selected = true
    }
    if (item.type === 4 && (childItem.shopCartNum < childItem.mustQty || childItem.shopCartNum < childItem.mqq || (childItem.availableQty !== null && childItem.shopCartNum > childItem.availableQty))) {
      item.isNotmm = true // isNotmm不符合最低订量或者起订量
    }
    return childItem
  })
  item.ifEdit = false
  item.keyProductPrice = 0
  item.promotionTotalPrice = 0
  setCart(item, isManual)
  return getLocalCart()
}

export function setProduct (item, isManual = 1) {
  setCart(item, isManual)
  return getLocalCart()
}

export function getLocalCart () {
  return localStore.get('TUP_cart') || {
    'promotion': [],
    'product': [],
    'giftProducts': [],
    'needGiftProduct': 1
  }
}

export function clearLocalCart () {
  return localStore.set('TUP_cart', {
    'promotion': [],
    'product': [],
    'giftProducts': [],
    'needGiftProduct': 1
  })
}

export function saveLocalCart (type, arr, isManual = 1) { // 整体保存，购物车页用到，type：1为促销，arr为promotion数组，type：2为常规，arr为product数组，type：3都保存，arr为对象，包含promotion，product两个属性
  let cart = getLocalCart()
  let promotion = type === 1 ? arr : type === 3 ? arr.promotion : cart.promotion
  let product = type === 2 ? arr : type === 3 ? arr.product : cart.product
  localStore.set('TUP_cart', {
    'promotion': promotion,
    'product': product,
    'giftProducts': cart.giftProducts,
    'needGiftProduct': cart.needGiftProduct
  })
  sendLineCart(isManual)
  return getLocalCart()
}

function sendLineCart (isManual) {
  let cartData = getLocalCart()
  let data = {}
  data.promotions = cartData.promotion
  data.products = cartData.product
  data.giftProducts = cartData.giftProducts
  data.needGiftProduct = cartData.needGiftProduct
  data.isManualAddCart = isManual // 是否手动加购物车 1：是 0：否，0是在登录时加入满单以及指定赠送时会用到
  if (!isManual && !data.promotions.length && !data.products.length) {
    return
  }
  vueItem.$tupHttp(vueItem, 'front/shoppingCart/refresh', 'POST', data).then(res => {
  })
  return cartData
}

function setCart (item, isManual = 1) { // 订货页用到
  let cart = getLocalCart()
  if (item) {
    let arr = typeof item.promotionRuleVO !== 'undefined' ? cart.promotion : cart.product
    arr = unique(arr, item)
    saveLocalCart(typeof item.promotionRuleVO !== 'undefined' ? 1 : 2, arr, isManual)
  }
}

function unique (arr, item) {
  if (arr.length) {
    for (let i = 0, len = arr.length; i < len; i++) {
      if (typeof item.promotionRuleVO === 'undefined' && arr[i].pCode === item.pCode) { // pCode判断常规产品
        if (item.shopCartNum === 0) {
          arr.splice(i, 1)
        } else {
          arr[i].shopCartNum = item.shopCartNum
        }
        return arr
      } else if (typeof item.promotionRuleVO !== 'undefined' && arr[i].id === item.id) { // id判断促销
        if (item.shopCartNum === 0) {
          arr.splice(i, 1)
        } else {
          arr[i].promotionRuleVO = item.promotionRuleVO
        }
        return arr
      } else if (i === len - 1) {
        if (item.type === 3) { // 满单在后面添加
          arr.push(item)
        } else {
          arr.unshift(item)
        }
      }
    }
  } else {
    arr.unshift(item)
  }
  return arr
}



