const estimateList = resolve => require(['@/components/estimate/estimateList'], resolve)
const estimate2019 = resolve => require(['@/components/estimate/estimate2019'], resolve)
const estimate2020 = resolve => require(['@/components/estimate/estimate2020'], resolve)
const estimateResult = resolve => require(['@/components/estimate/estimateResult'], resolve)
const dbTarget = resolve => require(['@/components/estimate/dbTarget'], resolve)
const estimateResult2020 = resolve => require(['@/components/estimate/estimateResult2020'], resolve)
const dbTargetResult = resolve => require(['@/components/estimate/dbTargetResult'], resolve)
let router = [
  {
    path: '/estimateList',
    name: 'estimateList',
    component: estimateList
  },
  {
    path: '/estimate2019',
    name: 'estimate2019',
    component: estimate2019
  },
  {
    path: '/estimate2020',
    name: 'estimate2020',
    component: estimate2020
  },
  {
    path: '/estimateResult',
    name: 'estimateResult',
    component: estimateResult
  },
  {
    path: '/dbTarget',
    name: 'dbTarget',
    component: dbTarget
  },
  {
    path: '/estimateResult2020',
    name: 'estimateResult2020',
    component: estimateResult2020
  },
  {
    path: '/dbTargetResult',
    name: 'dbTargetResult',
    component: dbTargetResult
  }
]
export default router



