const hccList = resolve => require(['@/components/hcc/hccList'], resolve)
const hccCharts = resolve => require(['@/components/hcc/hccCharts'], resolve)
const wechatHccList = resolve => require(['@/components/hcc/wechatHccList'], resolve)
let router = [
  {
    path: '/hccList',
    name: 'hccList',
    component: hccList
  },
  {
    path: '/hccCharts',
    name: 'hccCharts',
    component: hccCharts,
    meta: {
      title: '特百惠圈粉达人挑战赛排行榜',
      requiresAuth: false
    }
  },
  {
    path: '/wechatHccList',
    name: 'wechatHccList',
    component: wechatHccList,
    meta: {
      title: '特百惠圈粉达人挑战赛',
      requiresAuth: false
    }
  }
]
export default router



