/* 【售后】 */
const afterSale = resolve => require(['@/components/afterSale/afterSale'], resolve)
const afterSaleList = resolve => require(['@/components/afterSale/afterSaleList'], resolve)
const afterSaleType = resolve => require(['@/components/afterSale/afterSaleType'], resolve)
const afterSaleApply = resolve => require(['@/components/afterSale/afterSaleApply'], resolve)
const afterSaleDetail = resolve => require(['@/components/afterSale/afterSaleDetail'], resolve)
const afterSaleEdit = resolve => require(['@/components/afterSale/afterSaleEdit'], resolve)
const afterSaleBatch = resolve => require(['@/components/afterSale/afterSaleBatch'], resolve)

let router = [
  {
    path: '/afterSale',
    name: 'afterSale',
    component: afterSale
  },
  {
    path: '/afterSaleList',
    name: 'afterSaleList',
    component: afterSaleList
  },
  {
    path: '/afterSaleType',
    name: 'afterSaleType',
    component: afterSaleType
  },
  {
    path: '/afterSaleApply',
    name: 'afterSaleApply',
    component: afterSaleApply
  },
  {
    path: '/afterSaleDetail',
    name: 'afterSaleDetail',
    component: afterSaleDetail
  },
  {
    path: '/afterSaleEdit',
    name: 'afterSaleEdit',
    component: afterSaleEdit
  },
  {
    path: '/afterSaleBatch',
    name: 'afterSaleBatch',
    component: afterSaleBatch
  }
]
export default router



