// util工具类
/* eslint no-extend-native: ["error", { "exceptions": ["Date", "String"] }] */

class Util {
    // constructor () {}
    deepClone(obj) {
        let result
        let oClass = this._isClass(obj)
        // 确定result的类型
        if (oClass === 'Object') {
            result = {}
        } else if (oClass === 'Array') {
            result = []
        } else {
            return obj
        }
        for (let key in obj) {
            let copy = obj[key]
            if (this._isClass(copy) === 'Object') {
                result[key] = this.deepClone(copy) // 递归调用
            } else if (this._isClass(copy) === 'Array') {
                result[key] = this.deepClone(copy)
            } else {
                result[key] = obj[key]
            }
        }
        return result
    }
    _isClass(o) {
        if (o === null) return 'Null'
        if (o === undefined) return 'Undefined'
        return Object.prototype.toString.call(o).slice(8, -1)
    }
    isPC() {
        var userAgentInfo = navigator.userAgent.toLowerCase()
        var Agents = [
            'android',
            'iphone',
            'symbianOS',
            'windows phone',
            'ipad',
            'ipod'
        ]
        var flag = true
        for (var v = 0; v < Agents.length; v++) {
            if (userAgentInfo.indexOf(Agents[v]) > 0) {
                flag = false
                break
            }
        }
        return flag
    }
    getMachine(int) {
        let u = navigator.userAgent
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
        var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
        if (isAndroid) {
            window.local_method.tupperwareAppAction(int)
        } else if (isIOS) {
            window.webkit.messageHandlers.tupperwareAppAction.postMessage(int)
        } else {
            return true
        }
    }
    // 原生方法：打开新web-view
    appOpenUrl(url, title) {
        let u = navigator.userAgent
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
        var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
        if (isAndroid) {
            window.local_method.appOpenUrl(url, title)
        } else if (isIOS) {
            window.webkit.messageHandlers.appOpenUrl.postMessage([url, title])
        } else {
            return true
        }
    }
    // 原生方法：分享 type 1为微信好友 2为朋友圈
    shareWebToWeChat(url, title, description, imageurl, type) {
        let u = navigator.userAgent
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
        var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
        if (isAndroid) {
            window.local_method.shareWebToWeChat(url, title, description, imageurl, type)
        } else if (isIOS) {
            window.webkit.messageHandlers.shareWebToWeChat.postMessage([url, title, description, imageurl, type])
        } else {
            return true
        }
    }
    rechargeResult(url, title) {
        let u = navigator.userAgent
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
        var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
        if (isAndroid) {
            window.local_method.rechargeResult(url, title)
        } else if (isIOS) {
            window.webkit.messageHandlers.rechargeResult.postMessage([url, title])
        } else {
            return true
        }
    }
    formatePrice(num) {
        return Math.round(num * 100) / 100
    }
    formatThousand(value) {
        var b = false
        if (!value) {
            return '0.00'
        }
        value = value.toString()
        if (value.indexOf('-') !== -1) {
            b = true
            value = value.substring(1, value.length)
        }
        if (/^-?[0-9]+(.[0-9]+)?$/.test(value)) {
            value = value.toString().replace(/^(\d*)$/, '$1.')
            value = (value + '00').replace(/(\d*\.\d\d)\d*/, '$1')
            value = value.replace('.', ',')
            var re = /(\d)(\d{3},)/
            while (re.test(value)) {
                value = value.replace(re, '$1,$2')
            }
            value = value.replace(/,(\d\d)$/, '.$1')
        }
        if (b) {
            value = '-' + value
        }
        return value
    }
    getMonthLastDay(value) {
        let date = new Date(value)
        let currentMonth = date.getMonth()
        let nextMonthFirstDay = new Date(date.getFullYear(), ++currentMonth, 1)
        let lastTime = new Date(nextMonthFirstDay - 1000 * 60 * 60 * 24)
        let day = lastTime.getDate()
        return value + '-' + day
    }
    callMobile(mobile) {
        let u = navigator.userAgent
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
        var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
        if (isAndroid) {
            window.local_method.phoneCall(mobile)
        } else if (isIOS) {
            window.webkit.messageHandlers.callMobile.postMessage(mobile)
        } else {
            return true
        }
    }
}

function plugin(Vue) {
    // debugger
    let ut = new Util()
    if (plugin.installed) {
        return
    }
    Object.defineProperties(Vue.prototype, {
        $util: {
            get() {
                return ut
            }
        }
    })
}

if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.use(plugin)
}

if (!String.prototype.trim) {
    // 清除字符串两端空格，包含换行符、制表符
    String.prototype.trim = function () {
        return this.replace(/(^[\s\n\t]+|[\s\n\t]+$)/g, '')
    }
}

Date.prototype.format = function (fmt) {
    var o = {
        'M+': this.getMonth() + 1,               // 月份
        'd+': this.getDate(),                    // 日
        'h+': this.getHours(),                   // 小时
        'm+': this.getMinutes(),                 // 分
        's+': this.getSeconds(),                 // 秒
        'q+': Math.floor((this.getMonth() + 3) / 3), // 季度
        'S': this.getMilliseconds()             // 毫秒
    }
    if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    for (var k in o) {
        if (new RegExp('(' + k + ')').test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
        }
    }
    return fmt
}

export default plugin



