import Vue from 'vue'
import Router from 'vue-router'

import estimate from './estimate'
import afterSale from './afterSale'
import customer from './customer'
import hcc from './hcc'

/* 惠生意h5页面 start */
// const activity = resolve => require(['@/components/activity/activity'], resolve)
// const activityList = resolve => require(['@/components/activityList/activityList'], resolve)

/* 惠生意h5页面 end */
Vue.use(Router)

/* 【登录】 */
const login = resolve => require(['@/components/login/login'], resolve)
const loginActive = resolve => require(['@/components/login/loginActive'], resolve)
const forgetPass = resolve => require(['@/components/login/forgetPass'], resolve)
const modifyPass = resolve => require(['@/components/login/modifyPass'], resolve)
const emailActiveResult = resolve => require(['@/components/login/emailActiveResult'], resolve)

/* 【订货】 */
const shopping = resolve => require(['@/components/shopping/shopping'], resolve)
const search = resolve => require(['@/components/shopping/search'], resolve)
const refresh = resolve => require(['@/components/shopping/refresh'], resolve)

/* 【购物车】 */
const shoppingCart = resolve => require(['@/components/shopping/shoppingCart'], resolve)
const orderConfirm = resolve => require(['@/components/shopping/orderConfirm'], resolve)

/* 【周报月报】 */
const weekReport = resolve => require(['@/components/weekReport/weekReport'], resolve)
const weekReportList = resolve => require(['@/components/weekReport/weekReportList'], resolve)
const weekReportDetail = resolve => require(['@/components/weekReport/weekReportDetail'], resolve)

/* 【我的】 */
const mine = resolve => require(['@/components/mine/mine'], resolve)
const openApply = resolve => require(['@/components/mine/openApply'], resolve)
const openApplyProgress = resolve => require(['@/components/mine/openApplyProgress'], resolve)
const completeApply = resolve => require(['@/components/mine/completeApply'], resolve)
const basicInfo = resolve => require(['@/components/mine/basicInfo'], resolve)
const info = resolve => require(['@/components/mine/info'], resolve)
const fwi = resolve => require(['@/components/mine/fwi'], resolve)
const contract = resolve => require(['@/components/mine/contract'], resolve)
const bill = resolve => require(['@/components/mine/bill'], resolve)
const discount = resolve => require(['@/components/mine/discount'], resolve)
const discountDetail = resolve => require(['@/components/mine/discountDetail'], resolve)
const exchange = resolve => require(['@/components/mine/exchange'], resolve)
const exchangeDetail = resolve => require(['@/components/mine/exchangeDetail'], resolve)
const recharge = resolve => require(['@/components/mine/recharge'], resolve)
const rechargeList = resolve => require(['@/components/mine/rechargeList'], resolve)
const rechargeDetail = resolve => require(['@/components/mine/rechargeDetail'], resolve)
const onlineRecharge = resolve => require(['@/components/mine/onlineRecharge'], resolve)
const rechargeResult = resolve => require(['@/components/mine/rechargeResult'], resolve)
const offlineRecharge = resolve => require(['@/components/mine/offlineRecharge'], resolve)
const order = resolve => require(['@/components/mine/order'], resolve)
const orderDetail = resolve => require(['@/components/mine/orderDetail'], resolve)
const temporaryOrder = resolve => require(['@/components/mine/temporaryOrder'], resolve)
const restoreFromOrder = resolve => require(['@/components/mine/restoreFromOrder'], resolve)
const ICBCrecharge = resolve => require(['@/components/mine/ICBCrecharge'], resolve)
const maintain = resolve => require(['@/components/mine/maintain'], resolve)

/* 【库存盘点】 */
const stockList = resolve => require(['@/components/stock/stockList'], resolve)
const addStock = resolve => require(['@/components/stock/addStock'], resolve)
const stockPreview = resolve => require(['@/components/stock/stockPreview'], resolve)
const stockView = resolve => require(['@/components/stock/stockView'], resolve)

let router = new Router({
    routes: [
        {
            path: '/',
            redirect: '/shopping'
        },
        {
            path: '/login',
            name: 'login',
            component: login,
            meta: {
                requiresAuth: false
            },
            children: [
                {
                    path: '/loginActive',
                    name: 'loginActive',
                    component: loginActive,
                    meta: {
                        requiresAuth: false
                    }
                },
                {
                    path: '/forgetPass',
                    name: 'forgetPass',
                    component: forgetPass,
                    meta: {
                        requiresAuth: false
                    }
                },
                {
                    path: '/modifyPass',
                    name: 'modifyPass',
                    component: modifyPass
                }
            ]
        },
        {
            path: '/emailActiveResult',
            name: 'emailActiveResult',
            component: emailActiveResult,
            meta: {
                requiresAuth: false
            }
        },
        {
            path: '/shopping',
            name: 'shopping',
            component: shopping,
            meta: {
                hasNav: true
            }
        },
        {
            path: '/search',
            name: 'search',
            component: search
        },
        {
            path: '/refresh',
            name: 'refresh',
            component: refresh
        },
        {
            path: '/shoppingCart',
            name: 'shoppingCart',
            component: shoppingCart,
            children: [
                {
                    path: '/orderConfirm',
                    name: 'orderConfirm',
                    component: orderConfirm
                }
            ]
        },
        {
            path: '/weekReport',
            name: 'weekReport',
            component: weekReport,
            meta: {
                hasNav: true
            },
            children: [
                {
                    path: '/weekReportList',
                    name: 'weekReportList',
                    component: weekReportList,
                    children: [
                        {
                            path: '/weekReportDetail',
                            name: 'weekReportDetail',
                            component: weekReportDetail
                        }
                    ]
                }
            ]
        },
        {
            path: '/mine',
            name: 'mine',
            component: mine,
            meta: {
                hasNav: true
            }
        },
        {
            path: '/restoreFromOrder',
            name: 'restoreFromOrder',
            component: restoreFromOrder
        },
        {
            path: '/openApply',
            name: 'openApply',
            component: openApply
        },
        {
            path: '/openApplyProgress',
            name: 'openApplyProgress',
            component: openApplyProgress
        },
        {
            path: '/completeApply',
            name: 'completeApply',
            component: completeApply
        },
        {
            path: '/basicInfo',
            name: 'basicInfo',
            component: basicInfo
        },
        {
            path: '/info',
            name: 'info',
            component: info
        },
        {
            path: '/fwi',
            name: 'fwi',
            component: fwi
        },
        {
            path: '/contract',
            name: 'contract',
            component: contract
        },
        {
            path: '/order',
            name: 'order',
            component: order,
            children: [
                {
                    path: '/orderDetail',
                    name: 'orderDetail',
                    component: orderDetail
                }
            ]
        },
        {
            path: '/bill',
            name: 'bill',
            component: bill
        },
        {
            path: '/discount',
            name: 'discount',
            component: discount
        },
        {
            path: '/discountDetail',
            name: 'discountDetail',
            component: discountDetail
        },
        {
            path: '/exchange',
            name: 'exchange',
            component: exchange
        },
        {
            path: '/exchangeDetail',
            name: 'exchangeDetail',
            component: exchangeDetail
        },
        {
            path: '/temporaryOrder',
            name: 'temporaryOrder',
            component: temporaryOrder
        },
        {
            path: '/stockList',
            name: 'stockList',
            component: stockList
        },
        {
            path: '/addStock',
            name: 'addStock',
            component: addStock
        },
        {
            path: '/stockPreview',
            name: 'stockPreview',
            component: stockPreview
        },
        {
            path: '/stockView',
            name: 'stockView',
            component: stockView
        },
        {
            path: '/recharge',
            name: 'recharge',
            component: recharge
        },
        {
            path: '/rechargeList',
            name: 'rechargeList',
            component: rechargeList
        },
        {
            path: '/rechargeDetail',
            name: 'rechargeDetail',
            component: rechargeDetail
        },
        {
            path: '/onlineRecharge',
            name: 'onlineRecharge',
            component: onlineRecharge
        },
        {
            path: '/rechargeResult',
            name: 'rechargeResult',
            component: rechargeResult
        },
        {
            path: '/offlineRecharge',
            name: 'offlineRecharge',
            component: offlineRecharge
        },
        {
            path: '/ICBCrecharge',
            name: 'ICBCrecharge',
            component: ICBCrecharge
        },
        {
            path: '/maintain',
            name: 'maintain',
            component: maintain
        },
        ...estimate,
        ...afterSale,
        ...customer,
        ...hcc
    ]
})

router.beforeEach((to, from, next) => {
    if (typeof (to.meta.requiresAuth) === 'undefined' && !window.localStorage.getItem('TUP_token')) { // 需要登陆没有token跳登录页
        next({
            name: 'login',
            query: {
                redirect: to.fullPath
            }
        })
    } else {
        next()
    }
})

router.afterEach((to, from, next) => {
    // 判断是否为着陆页
    if (navigator.userAgent.toLowerCase().indexOf('iphone') !== -1 && !sessionStorage.getItem('landingUrl')) {
        sessionStorage.setItem('landingUrl', location.href)
    }
})
export default router



