
import XDialog from 'vux/src/components/x-dialog/index.vue'


import { mapActions } from 'vuex'
import localStore from 'util/localStore'

export default {
name: 'app',
data () {
return {
showLoading: false,
clientId: '',
password: '',
inited: false,
initCart: {},
redirect: '' 
}
},
watch: {
$route (to, from) {
this.redirect = to.path !== '/login' ? to.fullPath : '/' + to.query.redirect
}


















},
methods: {
_loginCustomer (clientId, psw, storeEmployeeGroup) { // 客户登记登录
let data = {
clientId: clientId,
pwd: psw,
storeEmployeeGroup: storeEmployeeGroup,
platform: 'WEB',
application: '会生意'
}
this.inited = true
this.$tupHttp(this, 'mss-store-service/storeEmployee/storeEmployeeLogin', 'POST', data).then(res => {
let data = res.data
localStore.set('TUP_token', data.extra.token)
localStore.set('TUP_pUid', storeEmployeeGroup ? data.model.pUid : data.extra.employeeCode)
localStore.set('TUP_psw', this.password)
localStore.set('TUP_pCitprovincialoffice', data.model.pCitprovincialoffice) 
localStore.set('TUP_pFirstOrderFlag', data.model.pFirstOrderFlag) 
localStore.set('TUP_pFirstorderDate', data.model.pFirstorderDate) 
localStore.set('TUP_pIsEmailActive', data.model.pIsEmailActive) 
localStore.set('TUP_pInfo', {
pCitoutletproperty2: data.model.pCitoutletproperty2
})
localStore.set('TUP_pCitorganlevel', data.model.pCitorganlevel) 
localStore.set('TUP_pAllowBusiness', data.model.pAllowBusiness) 


localStore.set('TUP_pActiveProvince', data.model.pActiveProvince) 
localStore.set('TUP_pActiveCity', data.model.pActiveCity) 
localStore.set('TUP_pActiveArea', data.model.pActiveArea) 
localStore.set('TUP_accountVisible', true)
this.redirect = this.redirect.indexOf('//') === 0 ? this.redirect.split('//')[1] : this.redirect
this.$router.replace({
path: this.redirect || '/customerIndex'
})
this.$util.getMachine(7) 
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_login (clientId, psw) {
let data = {
clientId: clientId,
password: psw,
platform: 'WEB',
application: '会生意'
}
this.inited = true
this.$tupHttp(this, 'front/login/', 'POST', data).then(res => {
let data = res.data
localStore.set('TUP_token', data.extra.token)
localStore.set('TUP_pUid', data.model.pUid)
localStore.set('TUP_psw', this.password)
localStore.set('TUP_pCitprovincialoffice', data.model.pCitprovincialoffice) 
localStore.set('TUP_pFirstOrderFlag', data.model.pFirstOrderFlag) 
localStore.set('TUP_pFirstorderDate', data.model.pFirstorderDate) 
localStore.set('TUP_pIsEmailActive', data.model.pIsEmailActive) 
localStore.set('TUP_pInfo', {
pCitoutletproperty2: data.model.pCitoutletproperty2
})
localStore.set('TUP_pCitorganlevel', data.model.pCitorganlevel) 
localStore.set('TUP_pAllowBusiness', data.model.pAllowBusiness) 


localStore.set('TUP_pActiveProvince', data.model.pActiveProvince) 
localStore.set('TUP_pActiveCity', data.model.pActiveCity) 
localStore.set('TUP_pActiveArea', data.model.pActiveArea) 
localStore.set('TUP_accountVisible', true)
this.$tupHttp(this, 'front/user/ossConfig').then(res => {
this.setOssConfig(res.data.model)
})
if (data.model.pCitorganlevel === '5') { // 分销商不允许下单
this.clearCart()
this.$router.replace({
path: this.redirect || '/'
})
this.$util.getMachine(7) 
} else {
this._getLineCart()
}
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_getLineCart () {
this.$tupHttp(this, 'front/shoppingCart/load').then(res => { // 获取线上购物车数据
this.$tupHttp(this, 'front/shoppingCart/getClientGiftProducts').then(rs => { // 获取指定赠送
let data = res.data.model
if (data) {
if (data.promotions && data.promotions.length) {
data.promotions = data.promotions.filter(i => {
return !(i.type === 3 || i.isMust === 1) 
})
}
if (data.products && data.products.length) {
data.products = data.products.filter(i => {
return !i.mustQty 
})
}
this.initCart = {
'promotion': data.promotions || [],
'product': data.products || [],
'giftProducts': rs.data.models || [],
'needGiftProduct': 1 // 是否需要指定赠送，默认1是
}
} else {
this.initCart = {
'promotion': [],
'product': [],
'giftProducts': rs.data.models || [],
'needGiftProduct': 1
}
}
this._selectMustPromotionsOrProducts() 
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_selectMustPromotionsOrProducts () { // 获取必订
this.$tupHttp(this, 'front/product/selectMustPromotionsOrProducts').then(res => {
let data = res.data.model
if (data) {
data.promotionItemVOS = data.promotionItemVOS.map(item => {
for (let i = 0; i < this.initCart.promotion.length; i++) { // 防止从必订修改为非必订，购物车出现重复促销id数据
if (item.id === this.initCart.promotion[i].id) {
this.initCart.promotion.splice(i, 1)
}
}
item.promotionRuleVO.productList.map(childItem => {
if (childItem.isHave) {
childItem.selected = true
}
if (childItem.mustQty) {
childItem.selected = true
childItem.shopCartNum = childItem.mustQty
}
if (item.type === 4) {
item.selected = true
childItem.selected = true
childItem.shopCartNum = childItem.productNum
if (childItem.shopCartNum < childItem.mustQty || childItem.shopCartNum < childItem.mqq || (childItem.availableQty !== null && childItem.shopCartNum > childItem.availableQty)) {
item.isNotmm = true 
}
}
return childItem
})
item.shopCartNum = 1
item.ifEdit = false
item.keyProductPrice = 0
item.promotionTotalPrice = 0
return item
})
this.initCart.promotion = this.initCart.promotion.concat(data.promotionItemVOS)
if (localStore.get('TUP_pFirstorderDate') !== null) { // 首订用户不允许加入常规产品
data.solrProductVos = data.solrProductVos.map(item => {
for (let i = 0; i < this.initCart.product.length; i++) { // 防止从必订修改为非必订，购物车出现重复促销id数据
if (item.pCode === this.initCart.product[i].pCode) {
this.initCart.product.splice(i, 1)
}
}
item.shopCartNum = item.mustQty
item.selected = true
return item
})
this.initCart.product = this.initCart.product.concat(data.solrProductVos)
}
}
this._getFullDiscount() 
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_getFullDiscount () {
this.$tupHttp(this, 'front/product/selectPromotionsByClientIdFromSolr?subType=0').then(res => {
let data = res.data.models
for (let i = 0, len = data.length; i < len; i++) {
data[i].promotionRuleVO.productList = []
data[i].ifEdit = false
data[i].keyProductPrice = 0
data[i].promotionTotalPrice = 0
this.initCart.promotion = this.initCart.promotion.concat(data[i])
}
localStore.set('TUP_cart', this.initCart)
this.saveCart({
type: 3,
item: {
product: this.initCart.product,
promotion: this.initCart.promotion
},
isManual: 0
})
this.redirect = this.redirect.indexOf('//') === 0 ? this.redirect.split('//')[1] : this.redirect
this.$router.replace({
path: this.redirect || '/'
})
this.$util.getMachine(7) 
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
...mapActions([
'saveCart',
'clearCart',
'setOssConfig'
])
},
created () {
if (!navigator.onLine) {
this.showLoading = true
}
window.addEventListener('online', () => {
this.showLoading = false
})
window.addEventListener('offline', () => {
this.showLoading = true
})
window.login = (clientId, psw, storeEmployeeGroup) => {
this.clientId = clientId
this.password = psw
if (clientId && psw) {
localStore.set('TUP_group', parseInt(storeEmployeeGroup)) 
if (parseInt(storeEmployeeGroup) === 0) {
this._loginCustomer(clientId, psw, parseInt(storeEmployeeGroup))
} else {
this._login(clientId, psw)
}
}
}
},
components: {

XDialog
}
}



