import * as types from './mutation-types'

const mutations = {
  [types.SET_DOT] (state, dot) {
    state.dot = dot
  },
  [types.SET_CART] (state, cart) {
    state.cart = cart
  },
  [types.SET_OSS_CONFIG] (state, ossConfig) {
    state.ossConfig = ossConfig
  }
}

export default mutations



