// http访问类
// debugger
let serviceIp = process.env.FMS_SERVICE_IP || '//' + window.location.hostname + ':8080'
let hhServiceIp = process.env.HH_SERVICE_IP
// serviceIp = 'https://api.tupperware.net.cn'
// debugger
class BaseHttp {
    constructor(serviceIp, service = '') {
        this.serviceIp = serviceIp
        this.service = service
    }
    getFunc() {
        let thiz = this
        let func = function (
            vm,
            url,
            method = 'GET',
            data = {},
            headers = {},
            moduleName = ''
        ) {
            if (url.indexOf('http') < 0 || url.indexOf('=http') > 0) { // 考虑到会把链接当成参数的情况，加入第二种情况
                url = thiz.serviceIp + thiz.service + moduleName + url
            }
            let promise = new Promise((resolve, reject) => {
                let pro = null
                if (method === 'POST') {
                    pro = vm.$http.post(url, data, headers)
                } else if (method === 'GET') {
                    pro = vm.$http.get(url, data, headers)
                } else if (method === 'PUT') {
                    pro = vm.$http.put(url, data, headers)
                } else if (method === 'DELETE') {
                    pro = vm.$http.delete(url, data, headers)
                }
                pro.then(
                    response => {
                        let codeArr = ['1410', '1411', '1415', '1102', '1420', '1421', '1422'] // 做另外处理的resultCode
                        if (response.data.success) {
                            resolve(response)
                        } else if (codeArr.indexOf(response.data.resultCode) >= 0) { // 特殊性：兼容提交订单接口
                            resolve(response)
                        } else {
                            vm.$vux.alert.show({
                                title: '错误提示',
                                content: response.data.message,
                                onHide() {
                                    if (response.data.resultCode === '403') {
                                        if (vm.$util.getMachine(6)) { // token认证过期告知原生，如果返回true，则为本地调试，跳转登录页面
                                            vm.$router.push({ // token认证过期跳转登录页面
                                                path: '/login',
                                                query: {
                                                    redirect: vm.$router.history.current.fullPath
                                                }
                                            })
                                        }
                                    }
                                }
                            })
                            reject(response)
                        }
                    },
                    error => {
                        let message = '请求出错'
                        if (process.env.NODE_ENV === 'production') message = '请求出错'
                        vm.$vux.toast.text(message)
                        reject(error)
                    }
                )
            })
            return promise
        }
        return func
    }
}

function plugin(Vue) {
    if (plugin.installed) {
        return
    }
    Object.defineProperties(Vue.prototype, {
        $tupHttp: { // fms
            get() {
                return new BaseHttp(serviceIp, '/').getFunc()
            }
        },
        $hsyHttp: { // hsy
            get() {
                return new BaseHttp(hhServiceIp, '/').getFunc()
            }
        },
        $epmsEnv: {
            get() {
                return window.epmsEnv
            }
        }
    })
}

if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.use(plugin)
}

export default plugin



