const customerIndex = resolve => require(['@/components/customer/customerIndex'], resolve)
const customerAdd = resolve => require(['@/components/customer/customerAdd'], resolve)
const customerList = resolve => require(['@/components/customer/customerList'], resolve)
const customerSRV = resolve => require(['@/components/customer/customerSRV'], resolve)
const nutritionAdd = resolve => require(['@/components/customer/nutritionAdd'], resolve)
const foodAdd = resolve => require(['@/components/customer/foodAdd'], resolve)
const potAdd = resolve => require(['@/components/customer/potAdd'], resolve)
let router = [
  {
    path: '/customerIndex',
    name: 'customerIndex',
    component: customerIndex
  },
  {
    path: '/customerAdd',
    name: 'customerAdd',
    component: customerAdd
  },
  {
    path: '/customerList',
    name: 'customerList',
    component: customerList
  },
  {
    path: '/customerSRV',
    name: 'customerSRV',
    component: customerSRV
  },
  {
    path: '/nutritionAdd',
    name: 'nutritionAdd',
    component: nutritionAdd
  },
  {
    path: '/foodAdd',
    name: 'foodAdd',
    component: foodAdd
  },
  {
    path: '/potAdd',
    name: 'potAdd',
    component: potAdd
  }
]
export default router



