import * as types from './mutation-types'
import { setCartDot, setPromotion, setProduct, saveLocalCart, clearLocalCart } from 'util/cart'

export const setDot = function ({commit, state}) {
  commit(types.SET_DOT, setCartDot())
}

export const setPromotionCart = function ({commit, state}, {item, isManual}) { // 对单个促销加入购物车做处理
  commit(types.SET_CART, setPromotion(item, isManual))
}

export const setProductCart = function ({commit, state}, {item, isManual}) { // 对常规产品加入购物车做处理
  commit(types.SET_CART, setProduct(item, isManual))
}

export const saveCart = function ({commit, state}, {type, item, isManual}) { // 保存促销或者常规产品type：1为促销，type：2为常规，type：3整体保存
  commit(types.SET_CART, saveLocalCart(type, item, isManual))
}

export const clearCart = function ({commit, state}) {
  commit(types.SET_CART, clearLocalCart())
}

export const setOssConfig = function ({commit, state}, ossConfig) {
  commit(types.SET_OSS_CONFIG, ossConfig)
}



