

import Vue from 'vue'
import FastClick from 'fastclick'
import App from './App'
import router from './router/index'
import store from './store'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import businessComponets from './assembly'
import tupHttp from './util/service'
import util from './util/index'

import VueLazyLoad from 'vue-lazyload'
import localStore from 'util/localStore'
import VueWechatTitle from 'vue-wechat-title'
import uweb from 'vue-uweb'
import VueClipboard from 'vue-clipboard2'

require('./util/rem.js')



Vue.use(ConfirmPlugin)
Vue.use(AjaxPlugin)
Vue.use(util)
Vue.use(AlertPlugin)
Vue.use(LoadingPlugin)
Vue.use(businessComponets)
Vue.use(ToastPlugin, { position: 'top' })
Vue.use(tupHttp)
Vue.use(VueLazyLoad, {
error: require('assets/images/default.png'),
loading: require('assets/images/default.png')
})
Vue.use(VueWechatTitle)
Vue.use(uweb, {
siteId: process.env.UMENG_ID
})
Vue.use(VueClipboard)

FastClick.attach(document.body)

Vue.config.productionTip = false


Vue.http.interceptors.request.use(function (config) {

config.headers.common['x_user_id'] = localStore.get('TUP_pUid')
config.headers.common['x_auth_token'] = localStore.get('TUP_token')
config.headers.common['x_request_platform'] = 'WEB'
config.headers.common['x_request_selectcount'] = 'mobile'
config.headers.common['x_request_application'] = 'fms_h5'
config.headers.common['x_province_code'] = localStore.get('TUP_pCitprovincialoffice')
config.headers.common['x_type'] = localStore.get('TUP_pFirstorderDate') === null ? 'true' : 'false' 
config.headers.common['x_employee_group'] = localStore.get('TUP_group')
return config
}, function (error) {
return Promise.reject(error)
})

/* eslint-disable no-new */
let vueItem = new Vue({
router,
store,
util,
tupHttp,
render: h => h(App)
}).$mount('#app')//没有el属性,$mount(’#app’) ：手动挂载到id为app的dom中的意思

window.addEventListener('resize', () => { // 针对周报输入问题
if (document.activeElement.tagName === 'INPUT' || document.activeElement.tagName === 'TEXTAREA') {
window.setTimeout(() => {
document.activeElement.scrollIntoViewIfNeeded()
}, 500)
}
})

export default vueItem



